#about_me {
    width: 60%;
    margin: 0px auto;
    display: flex;
    flex-direction: column;
    color: #b5a778;
    padding-top: 33px;
    padding-bottom: 105px;
    h2 {
        text-align: center;
        font-size: 34px;
        line-height: 42px; }
    p {
        font-size: 18px;
        line-height: 30px;
        text-align: center; } }

@media (max-width: 830px) {
    #about_me {
        width: 100%; } }


